import { onSendValidateAttachments } from "./compose/onSendValidateAttachments";

// import { logAttachments } from "./compose/logAttachments";

/* global global, Office, self, window */

Office.onReady(() => {});

function getGlobal() {
  return typeof self !== "undefined"
    ? self
    : typeof window !== "undefined"
    ? window
    : typeof global !== "undefined"
    ? global
    : undefined;
}

const g = getGlobal() as any;

// The add-in command functions need to be available in global scope
g.onSendValidateAttachments = onSendValidateAttachments;
